import React from "react";
import "./Jumbotron.css"


const Jumbotron = ({ children }) => (
  <div
    style={{ height: 400, clear: "both", paddingTop: 120, textAlign: "center" }}
    className="jumbotron-fluid"
  >
    
  </div>
);

export default Jumbotron;



